import React, { Component } from 'react';
import {Card, CardHeader, CardContent, TextField, Select, MenuItem, OutlinedInput, InputLabel, Button } from '@material-ui/core';
import SyntaxHighlighter from 'react-syntax-highlighter';


class InputCode extends Component {
  constructor(props){
    super(props);

    this.state = {
      language: this.props.language,
      code: this.props.code
    }

    this.languages = SyntaxHighlighter.supportedLanguages;
    this.languages.push('');
  }

  render() {
    var language = this.state.language;
    var code = this.state.code;

    return (
      <Card>
        <CardHeader title="Set Sample Code"/>
        <CardContent>
          <InputLabel style={{ "fontSize":12 }}>language</InputLabel>
          <Select
            fullWidth={true}
            input={<OutlinedInput name="language" />}
            value={language} onChange={e => this.setState({language: e.target.value})}
          >
            <MenuItem value={language}>
              <em>{language}</em>
            </MenuItem>
            {this.languages.map((language) =>
               <MenuItem value={language} key={'input_' + language}>{language}</MenuItem>
            )}
          </Select>
          
          <TextField 
            label="code" fullWidth={true} margin="normal" variant="outlined" multiline
            value={code} onChange={e => this.setState({code: e.target.value})} 
          />
          <Button
            type='submit' size='medium' variant='contained' color='primary'
            onClick={ () => this.props.setCode(this.state.language, this.state.code)}
          >
            Submit
          </Button>
        </CardContent>
      </Card>
    )
  }
}

export default InputCode;
